a {
  text-decoration: none;
  color: #E0ECFF;
}
.dip-box {
  border-top: 1px solid #203B60;
  font-weight: 500;
}
.dip-box .back-governance {
  border-top: 1px solid #203b60;
  display: flex;
  font-size: 24px;
}
.dip-box .back-governance img {
  width: 16px;
  height: 16px;
  margin-right: 16px;
}
.dip-box .back-governance .icon-back {
  height: 48px;
  display: flex;
  align-items: center;
  position: absolute;
  padding-left: 123px;
  padding-top: 120px;
}
.dip-box .back-governance .poll-detail {
  font-weight: 600;
  font-size: 32px;
  color: #FFF;
  margin: 0 auto;
  margin-top: 120px;
}
.dip-box .dip-box-info {
  width: 760px;
  margin: 0 auto;
  margin-top: 72px;
}
.dip-box .dip-box-info a {
  color: #cd7a37;
}
.dip-box .dip-box-info .poll-dip-info {
  height: 182px;
  background: #112247;
  border-radius: 8px;
  padding: 22px 30px;
}
.dip-box .dip-box-info .poll-dip-info .dip-list-title {
  color: #93A1B8;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
}
.dip-box .dip-box-info .poll-dip-info .dip-list-title .dip-executed,
.dip-box .dip-box-info .poll-dip-info .dip-list-title .dip-progress {
  display: flex;
  align-items: center;
}
.dip-box .dip-box-info .poll-dip-info .dip-list-title .dip-executed .executed-bg,
.dip-box .dip-box-info .poll-dip-info .dip-list-title .dip-progress .executed-bg {
  width: 12px;
  height: 12px;
  background-color: #CD7A37;
  border-radius: 50%;
  margin-right: 8px;
}
.dip-box .dip-box-info .poll-dip-info .dip-list-title .dip-executed .progress-bg,
.dip-box .dip-box-info .poll-dip-info .dip-list-title .dip-progress .progress-bg {
  width: 12px;
  height: 12px;
  background-color: #5577FD;
  border-radius: 50%;
  margin-right: 8px;
}
.dip-box .dip-box-info .poll-dip-info .dip-list-des {
  margin-top: 32px;
  min-width: 449px;
  height: 36px;
  display: flex;
  align-items: center;
  font-size: 18px;
  color: #E0ECFF;
}
.dip-box .dip-box-info .poll-dip-info .dip-time {
  font-size: 12px;
  color: #52617F;
  margin-top: 49px;
}
.dip-box .dip-box-info .dip-option {
  margin-top: 33px;
  height: 378px;
  background: #112247;
  border-radius: 8px;
  padding: 22px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.dip-box .dip-box-info .dip-option .option-check {
  width: 600px;
  height: 100%;
  padding-top: 12px;
  padding-left: 8px;
  padding-right: 21px;
  border: 1px solid transparent;
  border-radius: 8px;
  margin-top: 20px;
  cursor: pointer;
}
.dip-box .dip-box-info .dip-option .option-check .winner-status-quo {
  display: flex;
}
.dip-box .dip-box-info .dip-option .option-check .winner-status-quo .leading {
  margin-right: 8px;
}
.dip-box .dip-box-info .dip-option .option-check .status-quo {
  width: 100px;
  height: 17px;
  border-radius: 16px;
  border: 1px solid #FFF;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  margin-bottom: 10px;
}
.dip-box .dip-box-info .dip-option .option-check .leading {
  width: 100px;
  height: 17px;
  color: #59AE99;
  border-radius: 16px;
  border: 1px solid #59AE99;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  margin-bottom: 10px;
}
.dip-box .dip-box-info .dip-option .option-check .option-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 18px;
}
.dip-box .dip-box-info .dip-option .option-check .option-title .option-check-text {
  display: flex;
}
.dip-box .dip-box-info .dip-option .option-check .option-title .option-title-text {
  font-size: 18px;
}
.dip-box .dip-box-info .dip-option .option-check .option-title .option-num {
  font-size: 14px;
}
.dip-box .dip-box-info .dip-option .option-check .option-title input[type=radio] {
  position: absolute;
  clip: rect(0, 0, 0, 0);
}
.dip-box .dip-box-info .dip-option .option-check .option-title label {
  font-size: 32px;
  font-weight: 600;
  color: #e0ecff;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.dip-box .dip-box-info .dip-option .option-check .option-title input[type=radio] + label:before {
  content: "";
  display: inline-block;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  border: 1px solid #fff;
  margin-right: 13px;
}
.dip-box .dip-box-info .dip-option .option-check .option-title input[type=radio]:checked + label:before {
  background-clip: content-box;
  background-color: #fff;
  width: 8px;
  height: 8px;
  padding: 4px;
  border: 1px solid #fff;
}
.dip-box .dip-box-info .dip-option .option-check .option-peng-bg {
  margin-top: 8px;
  width: 0;
  height: 16px;
  border-radius: 8px;
  margin-bottom: 10px;
  background: radial-gradient(29.9% 70.94% at 44.25% 86.96%, rgba(89, 174, 153, 0.2) 0%, rgba(220, 141, 220, 0) 100%), radial-gradient(33.83% 53.57% at 35.87% 100%, rgba(89, 174, 153, 0.2) 0%, rgba(220, 141, 220, 0) 100%), radial-gradient(38.83% 66.43% at 51.21% 43.26%, rgba(89, 174, 153, 0.2) 0%, rgba(194, 166, 241, 0) 100%), linear-gradient(134.59deg, rgba(85, 119, 253, 0.5) 20.63%, rgba(205, 249, 232, 0) 47.84%), linear-gradient(220.99deg, rgba(85, 119, 253, 0.5) -15.86%, rgba(89, 174, 153, 0.5) 10.77%, rgba(89, 174, 153, 0.5) 29.79%), linear-gradient(128.53deg, rgba(85, 119, 253, 0.5) 28.63%, rgba(89, 174, 153, 0.5) 38.5%, rgba(89, 174, 153, 0.5) 50.26%), #5577FD;
}
.dip-box .dip-box-info .dip-option .option-check .option-one-bg {
  width: 31%;
}
.dip-box .dip-box-info .dip-option .option-check .option-two-bg {
  width: 69%;
}
.dip-box .dip-box-info .dip-option .option-check:hover {
  border: 1px solid #E27625;
}
.dip-box .dip-box-info .dip-option .option-check:hover .option-title-texte {
  color: #FFF;
}
.dip-box .dip-box-info .dip-option .option-checked {
  border: 1px solid #E27625;
}
.dip-box .dip-box-info .dip-option .option-checked .option-title-texte {
  color: #FFF;
}
.dip-box .dip-box-info .dip-option .vote-button {
  margin-top: 24px;
}
.dip-box .dip-box-info .dip-option .vote-button .your-power {
  margin-bottom: 10px;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.dip-box .dip-box-info .dip-option-des-rules {
  margin-top: 33px;
  margin-bottom: 24px;
  min-height: 600px;
  background: #112247;
  border-radius: 8px;
  padding: 22px 30px;
}
.dip-box .dip-box-info .dip-option-des-rules .description-text {
  color: #FFF;
  margin-top: 16px;
}
.dip-box .dip-box-info .dip-option-des-rules .description-text .ul-description ul {
  padding-left: 24px;
}
.dip-box .dip-box-info .dip-option-des-rules .description-text .ul-description ul li {
  margin-top: 24px;
}
.dip-box .dip-box-info .dip-option-des-rules .supplementary-notes {
  margin-top: 32px;
}
.dip-box .dip-box-info .dip-option-des-rules .supplementary-notes .notes-text {
  margin-top: 16px;
  color: #FFF;
}
.dip-box .dip-box-info .dip-option-des-rules .supplementary-notes .notes-text a {
  color: #cd7a37;
}
.dip-box .dip-box-info .dip-option-des-rules .vote-rules {
  margin-top: 32px;
}
.dip-box .dip-box-info .dip-option-des-rules .vote-rules .rules-text {
  color: #FFF;
}
.dip-box .dip-box-info .dip-option-des-rules .vote-rules .rules-text .rules-margin {
  margin-top: 16px;
}
.dip-box .dip-box-info .dip-option-des-rules .vote-rules .rules-text ul {
  padding-left: 24px;
}
.dip-box .dip-box-info .dip-option-des-rules .vote-rules .rules-text ul li {
  margin-top: 24px;
}
.dip-box .dip-box-info .dip-option-des-rules .vote-rules .rules-text .no-margin {
  margin: 0;
}
.dip-box .dip-box-info .dip-option-des-rules .vote-rules .rules-text .no-margin li {
  margin-top: 0;
}
@media only screen and (max-width: 770px) {
  .dip-box .back-governance .icon-back {
    padding-top: 16px;
    padding-left: 24px;
  }
  .dip-box .back-governance .poll-detail {
    margin-top: 48px;
  }
  .dip-box .dip-box-info {
    width: 375px;
  }
  .dip-box .dip-box-info .poll-dip-info {
    padding: 24px 16px;
  }
  .dip-box .dip-box-info .poll-dip-info .dip-list-des {
    width: 327px;
    min-width: 327px;
  }
  .dip-box .dip-box-info .dip-one-option {
    height: 500px;
  }
  .dip-box .dip-box-info .dip-option .option-check {
    width: 327px;
  }
  .dip-box .dip-box-info .dip-option .option-check .dip-one-option-title {
    height: 45px;
  }
}
